export default {
  data() {
    return {
      indicatorDots: true,
      autoplay: true,
      interval: 2000,
      duration: 500,
      indicatorActiveColor: '#fff',
      searchInfo: '大家都在搜“森海塞尔”',
      swiperList: [{
        imgUrl: "https://market.pd-unixe.com/uploads/20190625200421f6a525590.png",
        goods_id: 10255
      }],
      menuList: [], // 所有商品分类列表
      lightning: [{
        title: '限时购',
        time: '',
        newPrice: '',
        oldPrice: '',
        img: []
      }, {
        title: '秒杀购',
        time: '',
        newPrice: '',
        oldPrice: '',
        img: []
      }],
      recommendList: []
    }
  },
  onLoad() {
    // 获取所有商品分类
    this.getGoodscategory()
    
    // 获取推荐商品列表
    this.getRecommendgoods()
    
    // 获取一个限时购商品
    this.getLimitGoods()
    
    // 获取一个秒杀商品
    this.getKillGoods()
  },
  methods: {
    // 获取所有商品分类
    getGoodscategory() {
      this.$http({
        url: this.$api.goodscategory,
        cb: (err, res) => {
          if (!err && res.code === 1) {
            this.menuList = res.data.list
          } else if (res.code === 0 && res.msg) {
            uni.showToast({
              title: res.msg,
              icon: 'none'
            })
          } else {
            uni.showToast({
              title: '商品分类列表加载失败',
              icon: 'none'
            })
          }
        }
      })
    },
    // 商城页展示一个限时抢购商品
    getLimitGoods() {
      this.$http({
        url: this.$api.getflashsalegoodsbyone,
        cb: (err, res) => {
          if (!err && res.code === 1) {
            console.log('限时', res.data)
            if(res.data.goods) {
              this.lightning[0].oldPrice = res.data.goods.goods_max_price
              this.lightning[0].newPrice = res.data.goods.goods_min_price
              this.lightning[0].newPrice = res.data.goods.goods_min_price
              this.lightning[0].img[0] = res.data.goods.image[0].file_path
              this.lightning[0].img[1] = res.data.goods.image[1].file_path
            }
          } else if (res.code === 0 && res.msg) {
            uni.showToast({
              title: res.msg,
              icon: 'none'
            })
          } else {
            uni.showToast({
              title: '限时抢购商品加载失败',
              icon: 'none'
            })
          }
        }
      })
    },
    // 商城页展示一个秒杀商品
    getKillGoods() {
      this.$http({
        url: this.$api.getseckillgoodsbyone,
        cb: (err, res) => {
          if (!err && res.code === 1) {
            console.log('秒杀', res.data)
            if(res.data.goods) {
              this.lightning[1].oldPrice = res.data.goods.goods_max_price
              this.lightning[1].newPrice = res.data.goods.goods_min_price
              this.lightning[1].newPrice = res.data.goods.goods_min_price
              this.lightning[1].img[0] = res.data.goods.image[0].file_path
              this.lightning[1].img[1] = res.data.goods.image[1].file_path
              
              // this.lightning[0].time = res.data.goods.activity_endtime          时间待处理
            } else {
              this.lightning[1] = ''
            }
          } else if (res.code === 0 && res.msg) {
            uni.showToast({
              title: res.msg,
              icon: 'none'
            })
          } else {
            uni.showToast({
              title: '秒杀抢购商品加载失败',
              icon: 'none'
            })
          }
        }
      })
    },
    // 推荐商品列表
    getRecommendgoods() {
      this.$http({
        url: this.$api.recommendgoods,
        cb: (err, res) => {
          if(!err && res.code === 1) {
            this.recommendList = res.data.list
          } else if(res.code === 0 && res.msg) {
            uni.showToast({
              title: res.msg,
              icon: 'none'
            })
          } else {
            uni.showToast({
              title: '推荐商品列表获取失败',
              icon: 'none'
            })
          }
        }
      })
    },
    // 去商品详情页
    goDetail(item) {
      uni.navigateTo({
        url: '../components/goodDetail?info=' + item.info
        // url: 'goodDetail' 
      })
    },
    // 抢购或者秒杀页
    goPanicBuy(data) {
      switch (data) {
        case 0:
          uni.navigateTo({
            url: 'panicBuy?origin=' + '秒杀'
          })
          break
        case 1:
          uni.navigateTo({
            url: 'panicBuy?origin=' + '限时购'
          })
          break
      }
    },
    // 搜索页
    goSearch() {
      uni.navigateTo({
        url: '../components/search?type=1'
      })
    },
    // 商品分类
    goGoods(id) {
      uni.navigateTo({
        url: '../components/goods?id=' + id
      })
    },
    // 点击更多
    goMore() {
      uni.navigateTo({
        url: "../moreList/moreList"
      })
    }
  }
}
